* {
    margin: 0;
    padding: 0;
}
.active{
    background-color: #ff5000;
}
.active1{
    background-color: white;
}

.navi-bar{
    width: 100%;
    position: absolute;
    display: flex;
    z-index: 110;
    -webkit-box-pack: justify;
    justify-content: space-between;
    padding:2rem;
}
.btn-back{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 0.34rem;
    height: 0.34rem;
    border: 0;
    border-radius: 100%;
    text-align: center;
    color: rgba(0, 0, 0, 0.4);
}
.arrow{
    position: relative;
    font-size: 40px;
}
#spuare{
    position: relative;
    width: 100%;
}
#img-1{
    width: 100%;
    height: 100vw;
}
#ad{
    text-align: center;
    background-color: white;
    margin-top: -5px;
}
.bottom-bar{
    position: fixed;
    bottom: 0;
    width: 100%;
    height:50px;
    display: flex;
    z-index: 110;
    flex-direction: row;
    align-items: center;
    background-color: #fff;
    box-shadow: rgba(0, 0, 0, 0.15) 0 -0.01rem 0.06rem 0;
    padding-left: 0.1rem;
    padding-right: 0.1rem;
}
.btn-1{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 50px;
    height: 50px;
    white-space: nowrap;
    align-items: center;
    color: #999;
    padding: 0 0.1rem;
}


.taobao-iconfont {
    position: relative;
    top:-5px;
}
.f{
    font-size: 10px;
}
.btn-2{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 50px;
    height: 50px;
    white-space: nowrap;
    align-items: center;
    color: #999;
    padding: 0 0.1rem;
}
.icon-highlight {
    color: #ff5000;
}
.btn-text{
    font-size: 0.1rem;
    line-height: 0.2rem;
}
.bottom-bar-btn.btn-pos-left {
    border-radius: 1rem 0 0 1rem;
    background-image: linear-gradient(to right, #FFC500, #FF9402);
    text-align: center;
    line-height:30px ;
    width:100px;
    height:30px;
}
.btn-title {
    font-size: 15px;
    font-weight: bold;
    color: white;
}
.bottom-bar-btn.btn-pos-right {
    border-radius: 0 1rem 1rem 0;
    background-image: linear-gradient(to right, #FF7A00, #FE560A);
    width:100px;
    height:30px;
    text-align: center;
    line-height:30px ;
}

